.backgrounds {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.bg {
    flex-basis: 18%;
    margin-bottom: 10px;

    &__color {
        width: 100%;
        height: 50px;
        margin-bottom: 5px;
        display: block;
        border-radius: 5px;

        &--black {
            background: #151719;
        }

        &--fiolett {
            background: #452AA5;
        }

        &--orange {
            background: #FEA53A;
        }

        &--green {
            background: #52EDBB;
        }

        &--blue {
            background: #746DC8;
        }
    }


    .btn-block {
        width: 100%;
    }
}

@media only screen and (max-width : 1420px) {
    .bg {
        flex-basis: 32%;
    }

}

@media only screen and (max-width : 1200px) {
    .backgrounds {
        margin-bottom: 20px;
    }
}

@media only screen and (max-width : 590px) {
    .bg {
        flex-basis: 48%;
    }

   
}
